@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Montserrat"}:root{box-sizing:border-box}*,*::after,*::before{box-sizing:inherit;margin:0;padding:0}.demo{background-color:white;height:100vh;width:100vw;display:flex;border:1px solid green;position:relative}a{text-decoration:none;color:#212529;width:2em;height:-webkit-max-content;height:max-content}.demo-right,.demo-left{width:50%;height:100%;color:white;display:flex;justify-content:center;align-items:center}.demo-right{background-color:lightblue}.demo-left{background-color:orange}.description{margin:2em auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:white;max-width:500px;text-align:center}.description p{margin-top:2em;font-size:1.5rem}.description h2{margin-top:2em}

